.navbar {
    background: black;
    height: 60px;
    justify-content: center;
    position: fixed;
    top: 0;
    width: 100%;  
    color: white;
    opacity: 0.7;
  }
  
  .navbar ul {
    width: 100%;
  }
  
  .navbar li {
    display: inline-block;
    text-align: center;
    width: 33.33%;
    font-size: 1.8rem;
  }
  
  .navbar a {
    color: white;
    font-size: 1.8rem;
    text-decoration: none;
    font-family: "Aguafina Script";
  }
  